@import url(https://fonts.googleapis.com/css2?family=Lora:wght@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lora:wght@600&family=Noto+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#keyboard {
    position: absolute;
    bottom: 0;
    width: 100%;
    background-color: #d5d9dc;
    padding: 20px 0;
    padding-bottom: 40px;
    display: flex;
    justify-content: center;
}

ul {
    list-style-type: none;
    /* width: 784px; */
    margin: 0 auto;
}

li {
    float: left;
    margin-bottom: 10px;
}


.key {
    /* padding: 15px 28px; */
    display: block;
    color: #aaa;
    /* font: bold 9pt arial; */
    text-decoration: none;
    text-align: center;
    /* width: 44px;
    height: 41px; */
    margin: 5px;
    background: #222;
    border-radius: 4px;
    border-top: 1px solid #222;
    box-shadow:
        inset 0 0 25px #333,
        0 1px 0 #000,
        0 2px 0 #222,
        0 2px 3px #333;
    text-shadow: 0px -1px 0px #000;
}

.key:active,
.keydown {
    color: #888;
    background: #222;
    margin: 7px 5px 3px;
    box-shadow:
        inset 0 0 25px #333,
        0 0 3px #333;
    border-top: 1px solid #000;
}

.fn span {
    display: block;
    margin: 14px 5px 0 0;
    text-align: right;
    font: bold 6pt arial;
    text-transform: uppercase;
}

#esc {
    margin: 6px 15px 0 0;
    font-size: 7.5pt;
    text-transform: lowercase;
}

#keyboard button {
    padding: 0;
}
li button span {
    display: block;
    padding: 15px 32px;
}

#qwerty li button span {
    display: block;
    /* margin: 13px 0 0; */
    text-transform: uppercase;
}

#qwerty li #tab span {
    text-align: left;
    margin: 23px 0 0 10px;
    font-size: 7.5pt;
    text-transform: lowercase;
}

#qwerty li .alt b {
    display: block;
    margin: 3px 0 0;
}

#qwerty li .alt span {
    margin: 2px 0 0;
}


#asdfg li button span {
    display: block;
    /* margin: 13px 0 0; */
    text-transform: uppercase;
}

#asdfg li .alt span {
    margin: 0;
    /* text-transform: lowercase; */
}

#asdfg li .alt b {
    display: block;
    margin: 3px 0 0;
}

#asdfg li #caps b {
    display: block;
    background: #999;
    width: 4px;
    height: 4px;
    border-radius: 10px;
    margin: 9px 0 0 10px;
    box-shadow: inset 0 1px 0 #666;
}

#asdfg li #caps span {
    text-align: left;
    margin: 10px 0 0 10px;
    font-size: 7.5pt;
}

#asdfg li #enter span {
    text-align: right;
    /* margin: 23px 10px 0 0;
    font-size: 7.5pt; */
}


#zxcvb li button span {
    display: block;
    /* margin: 13px 0 0; */
    text-transform: uppercase;
}

#zxcvb li .shiftleft span {
    text-align: left;
    margin: 23px 0 0 10px;
    font-size: 7.5pt;
    text-transform: lowercase;
}

#zxcvb li .shiftright span {
    text-align: right;
    margin: 23px 10px 0 0;
    font-size: 7.5pt;
    text-transform: lowercase;
}

#zxcvb li .alt b {
    display: block;
    margin: 4px 0 0;
}

#zxcvb li .alt span {
    margin: 0;
}


#bottomrow li #fn span,
#bottomrow li #control span,
#bottomrow li #optionleft span,
#bottomrow li #commandleft span {
    display: block;
    text-align: left;
    margin: 31px 0 0 8px;
    font-size: 7.5pt;
    text-transform: lowercase;
}

#bottomrow li #optionright span,
#bottomrow li #commandright span {
    display: block;
    text-align: right;
    margin: 31px 8px 0 0;
    font-size: 7.5pt;
    text-transform: lowercase;
}

#bottomrow ol li #left span,
#bottomrow ol li #right span,
#bottomrow ol li #up span,
#bottomrow ol li #down span {
    display: block;
    margin: 9px 0 0;
}

.fn {
    height: 26px;
    width: 46px;
}

#delete {
    /* width: 72px; */
    width: 100%;
}

#tab {
    width: 72px;
}

#caps {
    width: 85px;
}

#enter {
    /* width: 85px; */
    width: 100%;
}

.shiftleft,
.shiftright {
    width: 112px;
}

#fn,
#control,
.option,
.command,
#spacebar {
    height: 49px;
}

#control {
    width: 56px;
}

.option {
    width: 46px;
}

.command {
    width: 67px;
}

#spacebar {
    width: 370px;
}

#left img,
#up img,
#down img,
#right img {
    border: none;
}

ul ol {
    list-style-type: none;
}

#down {
    height: 23px;
}

#up,
#left,
#right {
    height: 24px;
}

#left,
#right {
    margin: 30px 5px 5px;
}

#left:active,
#right:active {
    margin: 32px 5px 3px;
}

#up {
    margin: 5px 5px 1px;
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 0px;
}

#up:active {
    margin: 8px 5px -2px;
}

#down {
    margin: 0 5px 5px;
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
}

#down:Active {
    margin: 3px 5px 4px;
}

#main {
    width: 700px;
    padding: 20px 50px;
    margin: 0 auto 50px;
    background: #fff;
    border-radius: 5px;
    -webkit-box-shadow: 0 1px 2px #aaa;
}

h1 {
    color: #888;
    text-align: center;
    font: bold 25pt/25pt arial;
    margin: 30px 0 60px;
}

h2 {
    color: #666;
    font: 13pt/0pt arial;
}

p {
    color: #999;
    font: 9pt/17pt arial;
    margin: 0 0 50px;
}

small {
    font: italic 8pt/12pt arial;
    color: #aaa;
    padding: 0 130px 0 0;
    display: block;
}

cite {
    display: block;
    padding: 0 0 30px;
    margin: 0 auto;
    text-align: center;
    color: #ccc;
    font: italic bold 8pt arial;
}

cite button {
    color: #e5e5e5;
    text-decoration: underline;
}

#main button {
    color: #007fff;
}

ul,
ol {
    /* height: 80px; */
    padding: 0px;
    margin: 0;
}

/* Micro Clearfix by Nicolas Gallagher - http://nicolasgallagher.com/micro-clearfix-hack */
/* For modern browsers */
.cf:before,
.cf:after {
    content: "";
    display: table;
}

.cf:after {
    clear: both;
}

.inner-board {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
#text-field {
    font-size: 2em;
    padding: 40px;
    background-color: rgba(0, 0, 0, 0.8);
    color: white;
    height: 296px;
}

#text-field .text-input::after  {
    content: '|';
    animation: blinker 1.5s linear infinite;
  }
  
  @keyframes blinker {
    50% {
      opacity: 0;
    }
  }

h1 {
    color: white;
    padding: 10px;
    background-color: black;
}

#message {
    padding: 0;
    margin: 0;
    font-weight: bold;
    font-size: 20px;
    color: white;
    padding: 10px;
    background-color: black;
}

/* ipdad = 1536 x 2048 pixels, 4:3 ratio */

* {
  padding: 0;
  margin: 0;
}

html,
body {
  height: 100%;
  touch-action: manipulation;

}

header {
  font-family: 'Roboto', sans-serif;
  background-color: whitesmoke;
  color: black;
  height: 80px;
  display: flex;
  /* flex-direction: column;
  justify-content: center; */
  align-items: center;
  /* DISABLE DUBBLE TAP ZOOM ON ANDROID */
  touch-action: manipulation;
}
header h2 {
  margin-left: 40px;
  font-size: 20px;
  font-weight: bold;
}

body {
  background: url(/escape-room-eethuis-vers/static/media/1280px-coffee-beans.adb916d4.jpg) no-repeat;
  background-size: cover;

  position: relative;
}

#root {
  height: 100%;
  /* overflow: hidden; */

  /* PREVENT TEXT SELECTION */
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

#content {
  position: absolute;
  top: 80px;
  left: 0;
  bottom: 0;
  right: 0;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#shared-lists {
  position: relative;
  display: flex;
  height: 70vh;
  width: 100%;
  justify-content: space-evenly;
}

.list-box {
  max-width: 380px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.draggable {
  font-size: 28px;
  font-family: 'Lora';
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  background-color: black;
}

.box {
  width: 380px;
  height: 60px;
  color: white;
  padding: 10px 20px;
  border: 4px solid black;
  border-radius: 20px;
}

.target {
  border-style: none;
  background-color: rgba(255, 255, 255, 0.5);
  border-style: dashed;
  border-width: 3px;
  border-color: white;
}

.hitting {
  background-color: rgba(255, 255, 255, 0.2)
}

.arrow {
  opacity: 0.5;
}

#check.hidden {
  display: none;
}

/* #content .showBtn {
  display: block;
} */

.wrong {
  background-color: #dc3545;
}

.correct {
  background-color: #28a745;
}

button {
  touch-action: manipulation;
  /* prevent double tap zoom */
  background-color: orange;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 18px;
  cursor: pointer;
  border-radius: .3em;
}
#check {
  font-size: 24px;
  font-weight: bold;
  border-radius: 20px;
    display: flex;
  justify-content: center;
  align-items: center;
  /* height: 60px; */
}
.center {
  display: flex;
  justify-content: center;
  margin-top: 60px;
}
.backBtn {
  color: white;
  width: 160px;
  background-color: orange;
  border: none;
  border-radius: 0;
  font-weight: bold;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
button.spacebar{
  height: 53px;
  width: 400px;
}
#home {
  height: 100%;
}
button.app {
  margin: 40px;
  padding: 20px;
  font-size: 22px;
}
.finished{
  background-color: #28a745;
}
#countries {
display: flex;
flex-wrap: wrap;
justify-content: space-evenly;
}
#countries .box { 
    margin: 20px;
}
#game-content {
    height: calc(100% - 140px); 
    height: -o-calc(100% - 140px); /* opera */ /* google, safari */
    height: -moz-calc(100% - 140px); /* firefox */

}
#select-game #check, #type-game #check {
    position: absolute;
    top:0;
    right: 0;
    height: 80px;
    border-radius: 0;
}

